import React, { Component } from 'react'

class Skills extends Component {
    constructor(props)
    {
        super(props);
        this.state={
            'myskills':['Python','SQL','Tableau','Alteryx','Apache Spark', 'ReactJS', 'Git']
        };
    }

    render() {
        return (
            <div className="condiv skills">
                <a href="https://drive.google.com/file/d/15WYQ80Tg0Ro9vmZU2jY-2cbPE1t4Noe4/view?usp=sharing" target="_blank" rel="noopener noreferrer">
                <h2 className="subtopic" style={{color:"black"}}><u>Click to view my Resume</u></h2></a>
            <h3 className="subtopic">Key Skills</h3>
            <ul>
            {this.state.myskills.map((value)=>{
                return <li>{value}</li>
            })}
            </ul>
            </div>
            )
        }
    }
    
    export default Skills